@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
*{
  
  font-family: 'Courgette', cursive;
  
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-width: 295px;
  /* background: hsl(0, 0%, 98%); */
  background: hsl(0, 7%, 92%);
}
.search-and-filter{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 60px;
    margin: 60px 0px;
}

@media screen and (max-width:500px) {
    
    .search-and-filter{
        padding: 0 0;
        margin: 20px 0 20px 20px;
        flex-flow: column nowrap;
        align-items: center;
        /* justify-content: flex-start; */
        /* background : red; */
        height: 150px;

    }
}

.logo{
    height: 140px;
    width: 155px;
    margin-top: -1.6rem;
}
.header{
    max-width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    /* background: hsl(209, 23%, 22%); */
    background:hsl(0, 0%, 100%);
    height: 81px;
    box-shadow: 0 2px 5px  rgba(0, 0, 0, 0.2);
    
}
.header h2{
    color: white;
    margin-right: 5px;
}


.searchbox input{
    
    background: hsl(0, 0%, 100%);
    border: none;
    border-radius: 3px;
    height: 40px;
    width: 400px;
    box-shadow: 1px 0px 4px rgba(0,0,0,0.4);
    color : hsl(0, 1%, 28%);
    padding:20px;

    
}

::-webkit-input-placeholder{
    color:hsl(0, 4%, 23%);
    font-size: 15px;
}

:-ms-input-placeholder{
    color:hsl(0, 4%, 23%);
    font-size: 15px;
}

::-ms-input-placeholder{
    color:hsl(0, 4%, 23%);
    font-size: 15px;
}

::placeholder{
    color:hsl(0, 4%, 23%);
    font-size: 15px;
}

@media screen and (max-width:500px){
    .searchbox input{
        width: 250px;
    }
}
#selection{
    background-color: hsl(0, 0%, 100%);
    border: 3px solid hsl(0, 0%, 100%);
    border-radius: 3px;
    height: 40px;
    padding: 5px;
    box-shadow: 1px 0px 4px rgba(0,0,0,0.4);
    cursor: pointer
}

#selection:hover{
    color:skyblue;
    background-color: rgb(248, 248, 248);
    /* display: none; */
}
.card{
    background-color: hsl(0, 0%, 100%);
    width: 270px;
    height: auto;
    margin : 50px 50px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.4);
    transition: all 0.5s;
    border-radius: 4px;
}


.card:hover{
    transform: scale(1.05,1.05);
    /* transform: translate(50px, 50px); */
}

.card-wrapper{
    margin-left: 15px;
}

.line_height{
    line-height: 5px;   
    
}

.card_bold{
    font-weight: 600;
}

.line_height span{
    font-size: 14px;
    font-family: 'Balsamiq Sans', cursive;
}

.image-container{
    margin:0;
    padding:0;   
    height: 150px;
    width: 100%;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
.cards_container{
    
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    /* background: red; */
    /* height: 50px; */
}
.cardsdetail-container span,.right-part{
    font-family: 'Balsamiq Sans', cursive;
}

.back-button span{
    font-size: 18px;
    margin: 2px;

}

.back-button{
    
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid none;
    border-radius: 5px;
    width: 110px;
    height: 40px;;
    cursor: pointer;
    margin-bottom: 70px;
    background-color: hsl(0, 0%, 100%);
    box-shadow: 2px 1px 4px rgba(0,0,0,0.4);
}

.back-button:hover{
    background-color: black;
    color:white;
}


.cardsdetail-container{
    /* height:500px; */
    max-width: 100%;
    display: flex;
    flex-flow: column nowrap;
    /* background: skyblue; */
    margin: 60px 0 0 0;
    padding: 0 40px 0 40px;
}

.flag-image-container{
    display:flex;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height:350px;
    width:500px;
    transition: all 0.5s;
}

.flag-image-container:hover{
    transform: rotateY(90deg);
}

.image-detail-container{
    display: flex;
}

.detail-container{
    width: 700px;
    height: 350px;
    margin: 0 100px;
    display: flex;
    flex-flow: column nowrap;
    /* background: red; */
}

.card-detail-1{
    /* background:lawngreen; */
    height:160px;
    max-width: 700px;
    display: flex;
    flex-flow: column wrap;
    align-content: space-between
    /* justify-content: center; */
}

.b-card-1, .b-card-2{
    display:flex;
    flex-flow: row wrap;
    font-size:15px;
}

.b-card{
    text-align: center;
    display: inline-block;
    width: 70px;;
    border: none;
    margin:5px;
    padding : 6px 0px 0 0;
    height: 23px;
    border-radius: 3px;
    background-color: hsl(0, 0%, 100%);
    box-shadow: 2px 1px 4px rgba(0,0,0,0.4)
}

.card-detail-1 p{
    font-size: 18px;
    line-height: 3px;
    font-weight: bold;
    font-family: 'Balsamiq Sans', cursive;
}

.card-detail-2{
    margin-top: 20px;
}

.right-part, .left-part{
    font-family: 'Courgette', cursive;
}
.right-part{
    margin-left:10px;
    font-weight: 100;
}

.left-part{
    font-size: 16px;
}

.b-card-2 div{
    display:flex;
    align-items: center;
}
.b-card-1 div{
    display:flex;
    align-items: center;
}

.small-card-bottom{
    display:flex;
    flex-flow: row wrap;
    margin-left:15px;
}

/* Responsive Design   */

@media screen and (max-width: 1114px){
    .image-detail-container{
        display: flex;
        flex-flow: column nowrap;
        width: auto;
        height: auto;

    }
    .detail-container{
        width: auto;
        height: auto;
        margin: 0 10px;
        display: flex;
        flex-flow: column nowrap;
        /* background: red; */
    }
}

@media screen and (max-width:567px){
    .flag-image-container{
        display: block;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        width:295px;
        height: 170px;
    }

    .detail-container h1{
        margin-top: 60px;
    }
    .card-detail-1{
        margin-top: 40px;
        height:auto;
        max-width: auto;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
    }
    .cardsdetail-container{
        margin-left: 0px;
        
    }
    .image-detail-container{
        margin-left:-1.2rem;
        /* background: red; */
    }
    .back-button{
        margin-left:-1.2rem;
    }
}
.image-option-container{
    /* background: red; */
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
}

.options{
    border: 2px solid none;
    border-radius: 5px;
    display:flex;
    flex-flow: column nowrap;  
    margin:5px;
    
    background-color: hsl(0, 0%, 100%); 
    box-shadow: 2px 2px 4px rgba(0,0,0,0.4),
                -2px 0px 4px rgba(0,0,0,0.4);
    cursor: pointer;
    padding : 15px;
}

.options div{
    margin:5px;
    display:flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    min-height: 60px;
    min-width: 480px;
    background-color: rgb(113, 122, 133);
    font-size: 30px;
    border-radius: 3px;
    transition: all 0.3s;
}

.options div:hover{
    color:white;   
    transform: scale(1.02);
    box-shadow: 4px 4px 4px rgba(0,0,0,0.4);

}


.answer-header{
    display:flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    
}
.guess{
    font-size: 30px;
    margin-bottom: 10px;
}

div.right{
    background: green;
}
div.wrong{
    background-color: rgb(231, 15, 15);
    color: white;
    background-image: url(/Rest-Countries/static/media/skull.35b11766.png);
    background-repeat: no-repeat;
    background-position: center;
    
    background-size: 50px 50px; 
}

@media screen and (max-width:1120px){
    .answer-header{
        margin-top: 60px;
    }
}

@media screen and (max-width:567px){
    .answer-header{
        width: 295px;
        margin-top: 60px;
    }
    .options div{
        min-width: 295px;
    }
    .image-option-container{
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
    }
}
@media screen and (max-width: 400px){
    .options div{
        font-size: 25px;
        min-width: 265px;
    }
}
