.searchbox input{
    
    background: hsl(0, 0%, 100%);
    border: none;
    border-radius: 3px;
    height: 40px;
    width: 400px;
    box-shadow: 1px 0px 4px rgba(0,0,0,0.4);
    color : hsl(0, 1%, 28%);
    padding:20px;

    
}

::placeholder{
    color:hsl(0, 4%, 23%);
    font-size: 15px;
}

@media screen and (max-width:500px){
    .searchbox input{
        width: 250px;
    }
}